import { useEffect, useState } from "react";
import { extractToken } from "./Login/extractToken";
import { Login } from "./Login/Login";
import { RecommendationGrid } from "./Manga/RecommendationGrid";
import { ApolloProvider } from "@apollo/client";
import client from "./Common/apolloClient";

import 'tailwindcss/tailwind.css';
import { Footer } from "./Common/Footer";
import { Header } from "./Common/Header";

const App = () => {
  const [token, setToken] = useState<string | null>(localStorage.getItem('anilist_token'));

  useEffect(() => {
    const tokenFromUrl = extractToken();
    if (tokenFromUrl) {
      localStorage.setItem('anilist_token', tokenFromUrl); // Token im LocalStorage speichern
      setToken(tokenFromUrl);
      window.history.replaceState({}, document.title, window.location.pathname); // Entfernen des Tokens aus der URL und Beibehaltung des Pfads
    }
  }, []);

  return (
    <ApolloProvider client={client}>
      <div className="font-sans">
      <Header />
      <main className="app container pb-4 pl-4 pr-4 min-h-screen mx-auto">
        {token ? <RecommendationGrid /> : <Login />}
      </main>
      <Footer />
      </div>
    </ApolloProvider>
  );
};

export default App;