// src/components/MangaGrid.tsx

import React, { useEffect, useState } from 'react';
import { MediaRecommendation } from './Interfaces';
import { useQuery } from '@apollo/client';
import { GET_MEDIA_LIST, GetMediaListResult, MediaList, MediaListEntry, RecommendationEdge } from './queries';
import AnilistLogo from './AnilistLogo';
import { GET_USER_ID } from '../Login/queries';
import { debugLog } from '../Common/debugLog';
import { trackEvent } from '../Common/tracking';

interface WithScore {
  score: number
  [key: string]: any
}


function useRecomendations() {
  const [mediaType, setMediaType] = useState<"MANGA" | "ANIME">("MANGA");
  const [mediaRecommendations, setMediaRecommendations] = useState<MediaRecommendation[]>([]);
  const [userId, setUserId] = useState<number | null>(null);

  const { loading: userIdLoading } = useQuery(GET_USER_ID, {
    onCompleted: (data) => {
      setUserId(data.Viewer.id)
    },
    onError(error) {
      console.error(error)
      localStorage.removeItem('anilist_token');
      window.location.reload();
    },
  });
  const {
    loading: mediaListLoading,
    data: mediaListData,
    refetch: refetchMedia,
  } = useQuery(GET_MEDIA_LIST, {
    variables: { userId, type: mediaType },
    skip: !userId,
    onError(error) {
      console.error(error)
      localStorage.removeItem('anilist_token');
      window.location.reload();
    },
  });
  const hookIsLoading = userIdLoading || mediaListLoading;

  useEffect(() => {
    debugLog("mediaType changed", mediaType, userId, mediaListLoading);
    if (mediaListLoading || !userId) return;
    setMediaRecommendations([]);
    refetchMedia({ userId, type: mediaType })
  }, [mediaType]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (!mediaListData) return
    mediaListData as GetMediaListResult;

    let mediaList =
      mediaListData.MediaListCollection.lists
        .flatMap((mediaListGroup: MediaList) => mediaListGroup.entries)
        .map((entry: MediaListEntry) => entry.media)

    let recommendationDictionary = mediaListData.MediaListCollection.lists
      .flatMap((mediaListGroup: MediaList) => mediaListGroup.entries)
      .flatMap((ml: MediaListEntry) => ml.media.recommendations.edges.map((edge: any) => ({ ...edge, source: ml.media.id })))
      .filter((en: RecommendationEdge) => !(en == null || en.node == null || en.node.mediaRecommendation == null))
      .reduce((pre: { [x: string]: { id: any; title: any; imageUrl: any; score: number; }; },
        curr: RecommendationEdge & WithScore) => {
        if (pre[curr.node.mediaRecommendation.id]) {
          pre[curr.node.mediaRecommendation.id].score =
            pre[curr.node.mediaRecommendation.id].score + 1
        } else {
          pre[curr.node.mediaRecommendation.id] = {
            id: curr.node.mediaRecommendation.id,
            title: curr.node.mediaRecommendation.title.userPreferred,
            imageUrl: curr.node.mediaRecommendation.coverImage.large,
            score: 1
          }
        }
        return pre
      }, {})

    for (let media of mediaList) {
      delete recommendationDictionary[media.id]
    }

    let mappedMangas = Object.values(recommendationDictionary)
      .sort((a, b) => (b as WithScore).score - (a as WithScore).score)
      .map((el: any) => ({
        id: el.id,
        coverImageUrl: el.imageUrl,
        title: el.title,
        score: el.score
      } as MediaRecommendation))

    setMediaRecommendations(mappedMangas.slice(0, 100))
  }, [mediaListLoading, mediaListData]); // eslint-disable-line react-hooks/exhaustive-deps

  return { mediaType, setMediaType, mediaRecommendations, hookIsLoading }
}

export const RecommendationGrid: React.FC<{ className?: string }> = () => {
  const { mediaType, setMediaType, mediaRecommendations, hookIsLoading } = useRecomendations();
  const swtichMediaType = (mediaType: "ANIME" | "MANGA") => {
    trackEvent(`switchMediaType_${mediaType}`);
    setMediaType(mediaType)
  }


  if (hookIsLoading) {
    return (
      <div className="flex flex-col items-center justify-center text-white space-y-4 mt-8 bg-accent-200 p-12 rounded shadow">
        <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2"></div>
        <p className="text-2xl font-semibol">Loading your {mediaType.toLocaleLowerCase()} recommendations...</p>
      </div>
    );
  }

  return (
    <>
      <div className="recommendation-filters flex items-center mb-4">
        <button
          onClick={() => swtichMediaType("MANGA")}
          className={`px-4 py-2 rounded-l-full text-xs text-white ${mediaType === "MANGA" ? "bg-secondary-500" : "bg-gray-300"}`}
        >
          Manga
        </button>
        <button
          onClick={() => swtichMediaType("ANIME")}
          className={`px-4 py-2 rounded-r-full text-xs text-white ${mediaType === "ANIME" ? "bg-secondary-500" : "bg-gray-300"}`}
        >
          Anime
        </button>
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 mx-auto" style={{ display: 'grid' }}>
        {mediaRecommendations.map((media) => (
          MediaGridCard(media, mediaType)
        ))}
      </div>
    </>
  );
};

function MediaGridCard(media: MediaRecommendation, mediaType: "MANGA" | "ANIME" = "MANGA") {
  return <div key={media.id}
    className="bg-secondary rounded-lg shadow-md flex flex-col hover:bg-accent-300">
    <a href={`http://anilist.co/manga/${media.id}`}
      onClick={() => trackEvent(`openAnilist_${ mediaType}`)}
      target="_blank" rel="noopener noreferrer">
      <img src={media.coverImageUrl}
        alt={media.title}
        className="w-full h-48 object-contain"
        loading="lazy" />
    </a>
    <div className="p-4 flex-grow">
      <h3 className={`text-lg text-white font-semibold ${media.title.length > 20 ? 'text-sm' : ''}`}>{media.title}</h3>
    </div>
    <a
      href={`http://anilist.co/${mediaType.toLocaleLowerCase()}/${media.id}`}
      onClick={() => trackEvent(`openAnilist_${ mediaType}`)}
      target="_blank"
      rel="noopener noreferrer"
      className="inline-flex items-center px-4 py-2 text-sm font-medium text-white bg-primary-500 rounded-md hover:bg-primary-800"
    >
      <AnilistLogo className="w-4 h-4 mr-2" /> anilist.co
    </a>
  </div>;
}

